@import "../../styles/variables.scss";

section#about {
    background-color: $superlight;
    color: $secundary; 
}

section#about > div > p {
    color: $secundary;
}

section#about > div > p:first-of-type {
    font-weight: bold;
    font-family: arial; 
    color: $secundary;
}

.width80 {width:80%;}

.solid-left-side {
  border-left: $primary .5rem solid;
  padding: 1rem;
}

 

.columns {
  display: flex;
  flex-wrap: nowrap;  
}
 
div.columns > p:first-child{
  font-weight: bold;
  padding: 1rem;
  width: 20%;
}


/* Extra small devices (phones, 600px and down) */

@media only screen and (max-width: 600px) {
  .columns {
    flex-wrap: wrap;
  }
  div.columns > p:first-child{
    width: 100%; 
    font-size: 1.5rem;
  }
}

